import React from 'react';
import {observer} from 'mobx-react';
import {Affix, ConfigProvider, DatePicker, Radio, Table} from 'antd';
import moment from 'moment';
import * as mobx from 'mobx';
const {RangePicker} = DatePicker;
const customizeRenderEmpty = () => (
	<div style={{ textAlign: 'center',padding: '50px 0'}}>
		<img
			width='150px'
			height="100px"
			src='./images/not_found.png' />
		<p style={{
			fontSize:'14px',
			fontFamily:'PingFangSC-Regular,PingFang S',
			fontWeight: '400',
			color: 'rgba(75,80,81,1)'
		}}></p>
	</div>
);

@observer
class DingtalkRight extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			value: "day", // 
		}
	}

	componentDidMount() {
		let personParams = JSON.parse(JSON.stringify(this.store.personParams));
		this.store.personParams = Object.assign(personParams, {
			beginTime: moment().startOf('day').valueOf(),
			endTime: moment().endOf("day").valueOf()
		});
		this.store.getPersonScheduleResources();
	}

	onChangeTimeRange = (dates, dateStrings) => {
		this.setState({
			value: "",
		}, ()=>{
			let personParams = JSON.parse(JSON.stringify(this.store.personParams));
			this.store.personParams = Object.assign(personParams, {
				"beginTime": moment(dateStrings[0]).valueOf(),
				"endTime": moment(dateStrings[1]).valueOf(),
			});
			this.store.getPersonScheduleResources();
		});
	}

	// 
	onChange = e => {
		this.setState({
			value: e.target.value,
		}, () => {
			let personParams = JSON.parse(JSON.stringify(this.store.personParams));
			this.store.personParams = Object.assign(personParams, {
				"beginTime": moment().startOf(e.target.value).valueOf(),
				"endTime": moment().endOf(e.target.value).valueOf()
			});
			this.store.getPersonScheduleResources();
		});
	};

	render() {
		const columns = [
			{
				title: '',
				dataIndex: 'name',
				key: 'name',
				align: 'center',
				width: '14%',
				className: 'vt',
			},
			{
				title: '',
				dataIndex: 'effectiveHours',
				key: 'effectiveHours',
				align: 'center',
				width: '14%',
				className: 'vt',
			},
			{
				title: '',
				dataIndex: 'arrangeHours',
				key: 'arrangeHours',
				align: 'center',
				width: '14%',
				className: 'vt',
			},
			{
				title: 'h',
				dataIndex: 'surplusHours',
				key: 'surplusHours',
				align: 'center',
				width: '14%',
				className: 'vt',
			},
			{
				title: 'h',
				dataIndex: 'availableHours',
				key: 'availableHours',
				width: '14%',
				align: 'center',
				className: 'vt',
			},
			{
				title: '',
				align: 'center',
				children: [
					{
						title: '',
						dataIndex: 'effectiveTime',
						key: 'effectiveTime',
						align: 'center',
					},
					{
						title: '',
						dataIndex: 'silenceTime',
						key: 'silenceTime',
						align: 'center',
					},
				],
			}
		];
		const {value} = this.state;
		const {personParams, personResources} = this.store;
		const {beginTime, endTime} = personParams;
		return <div>
			<Affix offsetTop={60}>
				<div style={{
					width: '100%',
					height: 'auto',
					backgroundColor: '#FFF',
					paddingBottom: '15px'
				}}>
					<Radio.Group onChange={this.onChange} value={value} buttonStyle="solid">
						<Radio.Button value="day"></Radio.Button>
						<Radio.Button value="week"></Radio.Button>
						<Radio.Button value="month"></Radio.Button>
						<Radio.Button value="year"></Radio.Button>
					</Radio.Group>
					<RangePicker
						className="ml30"
						style={{width: '250px'}}
						allowClear={false}
						value={[moment(beginTime), moment(endTime)]}
						onChange={this.onChangeTimeRange}
					/>
				</div>
				<Table columns={columns}
				       dataSource={[]}
				       size={"small"}
				       pagination={false}
				       className="ci_table2 ci_table5 ci_table6"
				       bordered/>
			</Affix>
			<ConfigProvider renderEmpty={customizeRenderEmpty}>
				<Table
					className="mt15"
					columns={columns}
					dataSource={mobx.toJS(personResources)}
					bordered
					size={"small"}
					showHeader={false}
					pagination={false}
					className="ci_table2 ci_table5"
					rowClassName={(record, index) => {
						let className = index % 2 ? 'deep_gray' : 'shallow_gray';
						return className
					}}
				/>
			</ConfigProvider>
		</div>;
	}
}

export default DingtalkRight;



