import React from 'react';
import {observer} from 'mobx-react';
import * as mobx from 'mobx';
import _ from 'lodash';
import * as utils from "@/utils/utils";
import moment from "moment";
import {Scrollbars} from 'react-custom-scrollbars';
import {Breadcrumb, Row, Col, Checkbox, Drawer, Icon, Affix, Tabs, Table, ConfigProvider, DatePicker,} from "antd";

import ProjectCalendar from '@/components/bpm/ProjectCalendar';
import DingtalkTree from '@/components/bpm/DingtalkTree';
import DingtalkRight from '@/components/bpm/DingtalkRight';
import HeaderSearch from '@/components/HeaderSearch';
import BpmStore from "store/bpm/BpmStore";

const {TabPane} = Tabs;
const customizeRenderEmpty = () => (
	<div style={{textAlign: 'center', padding: '50px 0'}}>
		<img
			width='150px'
			height="100px"
			src='./images/not_found.png'/>
		<p style={{
			fontSize: '14px',
			fontFamily: 'PingFangSC-Regular,PingFang S',
			fontWeight: '400',
			color: 'rgba(75,80,81,1)'
		}}></p>
	</div>
);
const chinese = {1: "", 2: "", 3: "", 4: "", 5: "", 6: "", 0: ""};

@observer
export default class MyCalendar extends React.Component {
	constructor(props) {
		super(props);
		this.store = new BpmStore();
		this.state = {
			projectIndeterminate: false,  // 
			projectCheckAll: false,  // 
			projectChecked: [], // 
			personIndeterminate: false,  // 
			personCheckAll: false,  // 
			personChecked: [],  // 
			projectValue: '', //  // 
			projectFilterData: [],
			personValue: '', // 
			personFilterData: [],
			visible: false,
			days: 0, // 
			currentDay: moment().date(), // 
			date: moment(), // 
			tabKey: "0",
			tabKey2: "uuu", // 
		}
	}

	componentWillReceiveProps(nextProps) {

	}

	componentDidMount() {
		this.store.getUserInfoListByRoleCode();
		this.store.getCalendarProjectList();

		//   datedayscurrentDay
		const date = moment();
		const year = moment(date).year();
		const month = moment(date).month() + 1;
		this.setState({
			date,
			days: utils.fanDayByYearMonth(year, month), //
			currentDay: moment(date).date(), //
		});
		this.store.yearMonth = moment(date).format("YYYYMM");
	}

	onChange = (type, e) => {
		const {val, checked} = e.target;
		let projectChecked = JSON.parse(JSON.stringify(this.state[type + 'Checked']));
		if (checked) { // 
			projectChecked.push(val.id);
		} else {
			const index = projectChecked.findIndex(item => item == val.id);
			projectChecked.splice(index, 1);
		}
		this.setState({
			[type + 'Checked']: projectChecked,
			[type + 'Indeterminate']: !!projectChecked.length && projectChecked.length < mobx.toJS(this.store[type + "List"]).length,
			[type + 'CheckAll']: projectChecked.length === mobx.toJS(this.store[type + "List"]).length,
		}, () => {
			// 
			if (type == "project") {
				this.store.getProjectStagesByIds(projectChecked);
			}
			// 
			if (type == "person") {
				if (projectChecked.length > 0) {
					this.showDrawer();
					this.store.getPersonMonthTaskState(projectChecked[0]);
				} else {
					this.onClose();
				}
			}
		});
	};

	onCheckAllChange = (type, e) => {
		this.setState({
			[type + 'Indeterminate']: false,
			[type + 'CheckAll']: e.target.checked,
			[type + 'Checked']: e.target.checked ? mobx.toJS(this.store[type + "List"]).map(item => item.id) : [],
		}, () => {
			// 
			if (type == "project") {
				this.store.getProjectStagesByIds(this.state[type + "Checked"]);
			}
			// 
			if (type == "person") {
				if (e.target.checked) {
					this.store.getPersonMonthTaskState(this.state[type + "Checked"][0]);
					this.showDrawer();
				} else {
					this.onClose();
				}
			}
		});
	};

	showDrawer = () => {
		this.setState({
			visible: true,
		});
	};

	onClose = () => {
		this.setState({
			visible: false,
		});
	};

	onSearch = (type, value) => {
		this.setState({
			[type + 'Value']: _.trim(value)
		}, () => {
			let dataArray = JSON.parse(JSON.stringify(mobx.toJS(this.store[type + 'List'])));
			let filterData = dataArray.filter(item => (item[type == "project" ? "name" : "realname"]==null?"":item[type == "project" ? "name" : "realname"]).indexOf(this.state[type + 'Value']) > -1)
			this.setState({
				[type + 'FilterData']: filterData
			});
		});
	}

	getCurrentBell = (endTime, now) => {
		let currentBell = null;
		const bell_yellow = {fontSize: "14px", color: "#e7b550", marginRight: "10px"};
		const bell_red = {fontSize: "14px", color: "#c4382c", marginRight: "10px"};
		const bell_blue = {fontSize: "14px", color: "#4da9de", marginRight: "10px"};
		let days = moment(now).diff(moment(endTime), 'days');
		if (days > 3) {
			currentBell = bell_red;
		} else if (days >= 2 && days <= 3) {
			currentBell = bell_yellow;
		} else {
			currentBell = bell_blue;
		}
		return currentBell;
	}

	// tab
	callback = (key) => {
		this.setState({
			tabKey: key
		});
		const {personList} = this.store;
		const {personChecked} = this.state;
		const personSelect = mobx.toJS(personList).filter((item) => {   // 
			return personChecked.includes(item.id)
		});
		const obj = personSelect[key];
		this.store.getPersonMonthTaskState(obj.id);
	}

	// 
	checkDate = (date) => {
		const current = date + 1;
		this.setState({
			currentDay: current
		});
	}

	// 
	handleChange = (date, dateString) => {
		const year = moment(date).year();
		const month = moment(date).month() + 1;
		this.setState({
			date,
			days: utils.fanDayByYearMonth(year, month), //
			currentDay: moment(date).date(), //
		}, () => {
			this.store.yearMonth = moment(date).format("YYYYMM");
			const {projectChecked, personChecked} = this.state;
			// 
			if (projectChecked.length > 0) {
				this.store.getProjectStagesByIds(projectChecked);
			}
			// 
			if (personChecked.length > 0) {
				this.callback("0")
			}
		});
	}

	// tab
	callback2 = (key) => {
		this.setState({
			tabKey2: key
		});
	}

	onRef = (ref) => {
		this.child = ref
	}

	// 
	handleSearch = (value) => {
		this.child.onChange(value);
	}

	handPersonChange = (value) => {
		this.setState({
			personChecked: value
		}, () => {
			if (value.length > 0) {
				this.showDrawer();
				const {personList} = this.store;
				const personSelect = mobx.toJS(personList).filter((item) => {   // 
					return value.includes(item.id)
				})
				this.store.getPersonMonthTaskState(personSelect[0].id);
			} else {
				this.onClose();
			}
		})
	}

	render() {
		const {yearMonth, projectStages, projectList, personList, personMonthTask} = this.store;
		const {tabKey2, tabKey, date, currentDay, days, projectCheckAll, projectIndeterminate, projectChecked, personChecked, projectValue, projectFilterData} = this.state;

		let isFilter = projectValue == "" || projectValue == null;
		const activeData = isFilter ? mobx.toJS(projectList) : projectFilterData;  // 
		const personSelect = mobx.toJS(personList).filter((item) => {   // 
			return personChecked.includes(item.id)
		})
		const columns = [
			{
				title: '',
				dataIndex: 'dataIndex',
				align: 'center',
				key: 'dataIndex',
			},
			{
				title: '',
				dataIndex: 'projectName',
				align: 'center',
				key: 'projectName',
			},
			{
				title: '',
				dataIndex: 'name',
				align: 'center',
				key: 'name',
			},
			{
				title: '',
				dataIndex: 'rate',
				align: 'center',
				key: 'rate',
				render: (text, record) => {
					return <span style={this.getCurrentBell(record.endTime, record.now)}>{text}%</span>
				}
			},
		];
		const dataSource = mobx.toJS(personMonthTask).filter((mitem) => {
			let day = currentDay <= 9 ? '0' + currentDay : currentDay;
			return yearMonth + day >= moment(mitem.beginTime).format("YYYYMMDD") && yearMonth + day <= moment(mitem.endTime).format("YYYYMMDD")
		}).map((item, index) => {
			return Object.assign(item, {dataIndex: index + 1})
		});
		return (
			<div className="w pr">
				<div className="con_bottom">
					<Breadcrumb separator=">" style={{margin: '0'}}>
						<Breadcrumb.Item href={"#/bpm/progresslist"}></Breadcrumb.Item>
						<Breadcrumb.Item>{tabKey2 == 'uuu' ? "" : ""}</Breadcrumb.Item>
					</Breadcrumb>
				</div>
				<Tabs className={"item_tabs"} activeKey={tabKey2} onChange={this.callback2}>
					<TabPane tab="" key="uuu">
						<Row style={{
							borderTop: '1px solid #EBEBEB',
							paddingTop: '10px'
						}}>
							<Col span="6">
								<Affix offsetTop={60}>
									<Scrollbars
										autoHide
										autoHideTimeout={1000}
										autoHideDuration={200}
										autoHeight
										autoHeightMin={0}
										autoHeightMax={'calc(100vh - 180px)'}
										thumbMinSize={30}
										universal={true}>
									<DingtalkTree
										userSearch={true}
										onRef={this.onRef}
										store={this.store}
										showLine={false}
									/>
									</Scrollbars>
								</Affix>
							</Col>
							<Col span="18"
							     style={{borderLeft: '1px solid #EBEBEB', minHeight: '350px', paddingLeft: '15px'}}>
								<DingtalkRight
									store={this.store}
								/>
							</Col>
						</Row>
					</TabPane>
					<TabPane tab="" key="yyy">
						<div className="w clearfix" style={{
							borderTop: '1px solid #EBEBEB',
							paddingTop: '10px'
						}}>
							<div className="fl" style={{width: '250px', display: 'block'}}>
								<div
									style={{
										height: 'calc(50vh - 140px)',
										padding: '0 15px',
									}}>
									<Checkbox
										indeterminate={projectIndeterminate}
										onChange={this.onCheckAllChange.bind(this, 'project')}
										checked={projectCheckAll}
										className=""
									>
										
									</Checkbox>
									<HeaderSearch
										className={`action search calendar_search`}
										placeholder={""}
										defaultValue=""
										dataSource={[]}
										onSearch={value => {
											this.onSearch('project', value);
										}}
										onPressEnter={value => {

										}}
									/>
									<div style={{
										height: 'calc(50vh - 180px)',
										borderTop: '1px dashed #EBEBEB',
										borderBottom: '1px dashed #EBEBEB',
									}}>
									<Scrollbars
										autoHide
										autoHideTimeout={1000}
										autoHideDuration={200}
										autoHeight
										autoHeightMin={0}
										autoHeightMax={'calc(50vh - 180px)'}
										thumbMinSize={30}
										universal={true}>
										{activeData.map((item, index) => {
											return <Checkbox key={'calendar' + index}
											                 val={item}
											                 checked={projectChecked.includes(item.id)}
											                 value={item.name}
											                 className="mycalendar_check"
											                 onChange={this.onChange.bind(this, 'project')}>
												<span className="mycalendar_span" title={item.name}>{item.name}</span>
											</Checkbox>
										})}
									</Scrollbars>
									</div>
								</div>
								<div style={{
									height: 'calc(50vh - 140px)',
									padding: '0 15px',
								}}>
									<span style={{marginRight: '8px'}}></span>
									<HeaderSearch
										className={`action search calendar_search`}
										placeholder={""}
										defaultValue=""
										dataSource={[]}
										onSearch={value => {
											this.handleSearch(value);
										}}
										onPressEnter={value => {

										}}
									/>
									<div style={{
										height: 'calc(50vh - 155px)',
										borderTop: '1px dashed #EBEBEB',
										borderBottom: '1px dashed #EBEBEB',
									}}>
										<Scrollbars
											autoHide
											autoHideTimeout={1000}
											autoHideDuration={200}
											autoHeight
											autoHeightMin={0}
											autoHeightMax={'calc(50vh - 155px)'}
											thumbMinSize={30}
											universal={true}>
											<DingtalkTree
												userSearch={false}
												onRef={this.onRef}
												store={this.store}
												showLine={false}
												handPersonChange={this.handPersonChange}
											/>
										</Scrollbars>
									</div>
								</div>
							</div>
							<div className="fl"
							     style={{
								     display: 'block',
								     borderLeft: '1px solid #EBEBEB',
								     minHeight: '150px',
								     padding: '15px 15px 15px 30px',
								     width: 'calc(100% - 250px)',
								     overflow: 'auto',
							     }}>
								<div
									style={{
										position: 'relative',
										top: '-15px',
									}}
									className="tc">
									<span
										style={{
											fontWeight: 'bold',
											fontSize: '16px',
											color: '#333'
										}}>{moment(date).format('YYYYMMDD')} {"" + chinese[moment(date).day()]} </span>
									<span className="pr" style={{top: '7px'}}>
										<DatePicker
											className="calendar_DatePicker"
											style={{
												position: 'relative',
												top: '3px',
												width: '50px',
												height: '10px'
											}}
											onChange={this.handleChange}/>
										<Icon
											style={{
												cursor: 'pointer',
												position: 'absolute',
												left: '1px',
												top: '-8px',
												width: '60px',
												height: '60px',
												fontSize: '24px',
												backgroundColor: '#FFF'
											}}
											type="calendar"/>
									</span>
								</div>
								<ProjectCalendar
									days={days}
									currentDay={currentDay}
									projectStages={mobx.toJS(projectStages)}
									yearMonth={yearMonth}
								/>
							</div>
						</div>
						<div
							className={!this.state.visible ? "hidden" : "active_drawer"}
							style={{
								zIndex: '998',
								height: "100vh",
								width: '30%',
								overflow: 'hidden',
								position: 'fixed',
								top: '0',
								right: '0',
								border: '1px solid #ebedf0',
								borderRadius: 0,
								background: '#fafafa',
							}}
						>
							<Drawer
								placement="right"
								closable={false}
								onClose={this.onClose}
								visible={this.state.visible}
								width={"100%"}
								getContainer={false}
								style={{position: 'absolute'}}
								bodyStyle={{
									padding: '0 0 0 20px'
								}}
							>
								<Tabs
									activeKey={tabKey}
									onTabClick={this.callback}
									tabPosition={'top'}
									style={{marginLeft: '10px',marginRight: '20px', minHeight: "300px", height: "auto"}}>
									{personSelect.map((item, index) => (
										<TabPane tab={`${item.realname}`} key={index} val={item}>
											<ConfigProvider renderEmpty={customizeRenderEmpty}>
												<Table columns={columns}
												       pagination={false}
												       size="middle"
												       bordered={false}
												       dataSource={dataSource}
												/>
											</ConfigProvider>
										</TabPane>
									))}
								</Tabs>
								<ul style={{position: 'absolute', top: '50px', left: '10px'}}>
									{[...Array(days).keys()].map(i => (
										<li key={i}
										    className={currentDay == (i + 1) ? "pointer personMonthTask_li_active" : "pointer personMonthTask_li"}
										    onClick={this.checkDate.bind(this, i)}>
											{i + 1}
										</li>
									))}
								</ul>
								<Icon
									onClick={this.onClose}
									title={""}
									className="pointer"
									style={{position: 'absolute', bottom: '10px', left: '30px'}}
									type="double-right"/>
							</Drawer>
						</div>
						{personSelect.length > 0 && !this.state.visible ?
							<Affix offsetBottom={'20px'} style={{position: 'fixed', right: '25px', bottom: '10px'}}>
								<Icon
									onClick={this.showDrawer}
									title={""}
									className="pointer"
									type="double-left"/>
							</Affix> : null}
					</TabPane>
				</Tabs>
			</div>
		)
	}
}




