/*
* 
* */
import React from 'react';
import moment from "moment";
import * as mobx from 'mobx';
import {Popover} from "antd";

// 
const colors = {0: '#EEF7FF', 1: '#4da9de', 2: '#e7b550', 3: '#c4382c', };

export default class ProjectCalendar extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			days: 1,
			currentDay: 2,
			CANVAS_WIDTH: 15 * 76,
			CANVAS_HEIGHT: 32 * 44,
		}
	}

	componentWillReceiveProps(nextProps) {
		const {projectStages} = nextProps;
		var width;
		if (projectStages.length>=15) {
			width = projectStages.length*76;
		} else {
			width = 15 * 76;
		}
		var GRID_WIDTH = 76;
		var GRID_HEIGHT = 44;
		const current = Number(nextProps.days)+1;
		const currentDay = Number(nextProps.currentDay)+1;
		this.setState({
			CANVAS_WIDTH:width ,
			CANVAS_HEIGHT: (current) * 44,
			days: current,
			currentDay: currentDay
		}, () => {
			const {CANVAS_WIDTH, CANVAS_HEIGHT} = this.state;
			this.drawGrid(CANVAS_WIDTH, CANVAS_HEIGHT, GRID_WIDTH, GRID_HEIGHT);
		});
	}

	componentDidMount() {
		const {projectStages} = this.props;
		var width;
		if (projectStages.length>=15) {
			width = projectStages.length*76;
		} else {
			width = 15 * 76;
		}
		var GRID_WIDTH = 76;
		var GRID_HEIGHT = 44;
		const current = Number(this.props.days)+1;
		const currentDay = Number(this.props.currentDay)+1;
		this.setState({
			CANVAS_WIDTH: width,
			CANVAS_HEIGHT: (current) * 44,
			days: current,
			currentDay: currentDay
		}, () => {
			const {CANVAS_WIDTH, CANVAS_HEIGHT} = this.state;
			this.drawGrid(CANVAS_WIDTH, CANVAS_HEIGHT, GRID_WIDTH, GRID_HEIGHT);
		});
	}

	//
	drawGrid = (CANVAS_WIDTH, CANVAS_HEIGHT, GRID_WIDTH, GRID_HEIGHT) => {
		var rows = parseInt(CANVAS_WIDTH / GRID_WIDTH);
		//var cols=parseInt(CANVAS_HEIGHT/GRID_HEIGHT);
		var cols = parseInt(this.state.days);
		for (var i = 0; i < rows; ++i) {
			for (var j = 0; j < cols; ++j) {
				this.drawRect(i, j, GRID_WIDTH, GRID_HEIGHT);
			}
		}
	}

    //
	drawRect = (i, j, GRID_WIDTH, GRID_HEIGHT) => {
		var drawing = document.getElementById("drawing");
		if (!drawing.getContext) return false;
		var context = drawing.getContext("2d");
		context.lineWidth = 0.4;
		context.strokeStyle = "lightgrey";
		context.strokeRect(i * 76, j * 44, GRID_WIDTH, GRID_HEIGHT);
	}

	// 
    onMouseOver = (key) => {
	    console.log(key, '')
        //let ele = document.querySelector("."+key);
	    var ele = document.getElementById(key);
	    ele.style.display="block";
    }

    // 
    onMouseOut = (key) => {
       console.log(key, '')
       // let ele = document.querySelector("."+key);
	    var ele = document.getElementById(key);
	    ele.style.display="none";
    }

    // 
    onMouseMove = (key, offsetTop, eve) => {
	    console.log(key, eve, '')
        //let ele = document.querySelector("."+key);
	    var ele = document.getElementById(key);
	    let e=eve||window.event;
        ele.style.left=e.offsetX+5+"px";
        ele.style.top= e.offsetY+5+
            offsetTop+"px"; // poffsetlefttop+5php
    }

	render() {
		const {CANVAS_WIDTH, CANVAS_HEIGHT, days, currentDay} = this.state;
		const {projectStages, yearMonth} = this.props;
		const current =  moment().format("YYYYMM");
		const date = moment().date();//343
		return (
			<div className="pr" style={{width: CANVAS_WIDTH, height:'calc(100vh - 340px)', paddingLeft:'25px'}}>
				{/*x*/}
				<ul className="projectCalendar_row">
					{projectStages&&projectStages.map((item, index) => {
						return <li key={'row' + index} title={item.name}>
							{item.name}
						</li>
					})}
				</ul>
				{/*y*/}
				<ul className="projectCalendar_col">
					{[...Array(days-1).keys()].map(i => (
						<li key={'col' + i}
						    style={{color: (yearMonth==current)&&(date)==(i+1)?"#FB602D":"#333"}}
						>
							{i + 1}
						</li>
					))}
				</ul>
				{/**/}
				<div
					style={{
						width: CANVAS_WIDTH,
						height: '2px',
						backgroundColor: '#FB602D',
						position: 'absolute',
						left: '25px',
						top: (date+1) * 44 + "px"
					}}
					className={yearMonth==current?"projectCalendar_baseline":"hidden"}></div>
				{/*canvas*/}
				<canvas id="drawing" style={{overflowX: 'auto'}} width={CANVAS_WIDTH} height={CANVAS_HEIGHT}></canvas>
				{/**/}
				<div style={{
					width: CANVAS_WIDTH,
					height: CANVAS_HEIGHT,
					position: 'absolute', top: '0', left: '0'
				}}>
					{projectStages && projectStages.map((item, index) => {
						return (item.children && item.children.map((mitem, mindex) => {
							//  
							const month = Number(yearMonth);  // 
							const beginMonth = mitem.beginTime==0?month:moment(mitem.beginTime).format("YYYYMM");  // 
							const endMonth = mitem.endTime==0?month:moment(mitem.endTime).format("YYYYMM");  // 
							let end = endMonth>month?moment(yearMonth+ (this.state.days-1)):mitem.endTime; // 
							let begin = beginMonth<month?moment(yearMonth+'01'):mitem.beginTime;   // 
							let days = mitem.beginTime==0?1:moment(end).diff(moment(begin), 'days');
							// 
							let startDay = mitem.beginTime==0?moment(mitem.endTime).date()-1:(beginMonth<month?1:moment(mitem.beginTime).date());
							return (
								<Popover content={<div
									style={{
										width:'200px',
										background: '#FFF',
										margin: 0,
										textAlign:'left',
										color: '#333',
										padding: '0px 5px',
										zIndex:'9999'
									}}
									id={mitem.id}>
									<p>{mitem.stageName}</p>
									<p>{moment(mitem.beginTime).format("YYYY-MM-DD")}</p>
									<p>{moment(mitem.endTime).format("YYYY-MM-DD")}</p>
									{mitem.rate==null||mitem.rate==0?null:<p>:{mitem.rate}%</p>}
								</div>}>
									<div
										key={mitem.id}
										className="project_div"
                                       // onMouseOver={()=>this.onMouseOver(mitem.id)}
                                       // onMouseMove={()=>{this.onMouseMove(mitem.id, (startDay-1)*44, event)}}
                                       // onMouseOut={()=>this.onMouseOut(mitem.id)}
										style={{
											position: 'absolute',
											top: (startDay+1)*44+'px',  // x   index 
											left: index*76+25+'px', // y   index 
											width: '76px',
											height: 44 * days + 'px',  // 
											zIndex: 8+mindex+1, // 
										}}>
											<div
												className="ellsis"
												style={{
													position: 'absolute',
													top: 0,
													left: 0,
													right: 0,
													width: '100%',
													zIndex:'999'
												}}>{mitem.stageName}</div>
											<div
												style={{
													position: 'absolute',
													top: 0,
													left: 0,
													right: 0,
													zIndex:'888',
													backgroundColor: colors[mitem.taskState],
													height: mitem.rate+"%"
												}}
												className="project_progress"></div>
                                        <div
                                            style={{
                                                width:'200px',
                                                background: '#FFF',
                                                margin: 0,
                                                display: 'none',
                                                position: 'absolute',
                                                left: 0,
                                                top:0,
                                                textAlign:'left',
                                                color: '#333',
                                                padding: '3px 15px',
                                                zIndex:'9999'
                                            }}
                                            id={mitem.id}>
                                            <p>{mitem.stageName}</p>
                                            <p>{moment(mitem.beginTime).format("YYYY-MM-DD")}</p>
                                            <p>{moment(mitem.endTime).format("YYYY-MM-DD")}</p>
                                            {mitem.rate==null||mitem.rate==0?null:<p>:{mitem.rate}%</p>}
                                        </div>
									</div>
								</Popover>
                            )
						}))
					})}
				</div>
			</div>
		)
	}
}



